import "react-tooltip/dist/react-tooltip.css";
import { Link, useParams } from "react-router-dom";
import { Tooltip } from "react-tooltip";
import {
	Box,
	Container,
	StyledHeaderText,
	StyledLi,
	StyledList,
	StyledMapPoland,
	StyledProvinceButtonBox,
	StyledSvg,
	StyledUl,
} from "./styled";
import { useAllProvinces } from "./useAllProvinces";
import { useDispatch, useSelector } from "react-redux";
import {
	selectProvinceName,
	setProvinceName,
} from "../baseLocalData/teritorialUnitManyVariables/teritorialUnitManyVariablesSlice";
import Button from "@mui/material/Button";
import { useWindowSize } from "../../common/WindowSize";
import { Select } from "@mui/material";

export const MapProvinces = () => {
	const dispatch = useDispatch();
	const param = useParams();
	const allProvinces = useAllProvinces();
	const provinceName = useSelector(selectProvinceName);
	const [widthSize] = useWindowSize();
	const provinceNameList = (province) => {
		const data = province.mapProvincesName.map((region) => region.key);
		const sortedlist = [...data]
			.sort()
			.map((name) => <StyledLi>{name}</StyledLi>);

		return sortedlist;
	};

	// <StyledList>
	// 	{allProvinces.map((province) =>
	// 		province.name === provinceName ? (
	// 			<StyledUl key={province.id}>{province.mapProvincesName}</StyledUl>
	// 		) : (
	// 			""
	// 		),
	// 	)}
	// </StyledList>;

	return (
		<Container>
			<StyledProvinceButtonBox>
				<StyledHeaderText>
					WYBIERZ JEDNOSTKĘ TERYTORIALNĄ - POWIATY
				</StyledHeaderText>
				<Button
					variant="contained"
					size="small"
					onClick={() => dispatch(setProvinceName(""))}>
					Mapa Polski
				</Button>
			</StyledProvinceButtonBox>
			<Box>
				<StyledMapPoland>
					{allProvinces.map((province) =>
						provinceName === province.name ? (
							<>
								<StyledSvg
									version="1"
									preserveAspectRatio={`${province.preserveAspectRatio}`}
									viewBox={`${province.viewBox}`}
									stroke="white"
									width="100%"
									height="100%"
									key={province.id}>
									{province.mapLink}
								</StyledSvg>
								<Tooltip
									id="my-tooltip"
									style={{
										backgroundColor: "black",
										color: "white",
										padding: "5px 10px",
									}}
									place="bottom"
								/>
							</>
						) : (
							""
						),
					)}
				</StyledMapPoland>
				{widthSize >= 992 ? (
					<StyledList>
						{allProvinces.map((province) =>
							province.name === provinceName ? (
								<StyledUl key={province.id}>
									{province.mapProvincesName}
								</StyledUl>
							) : (
								""
							),
						)}
					</StyledList>
				) : (
					<Select
						className="basic-single"
						classNamePrefix="select"
						defaultValue={{
							value: "",
							label: "wybierz",
						}}
						isClearable={false}
						isSearchable={true}
						autoFocus={true}
						options={{ value: "province.name", label: " province.name" }}
						onChange={(target) => console.log(target)}></Select>
				)}
			</Box>
		</Container>
	);
};

