import { NavLink } from "react-router-dom";
import styled, { css } from "styled-components";

export const Container = styled.div`
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	padding: 20px;
	flex-direction: column;
	width: 50%;
	background-color: rgb(240, 240, 240);
	border: 1px solid rgb(203, 203, 203);
	border-radius: 20px;
	@media ((max-width: ${({ theme }) => theme.breakpoint.md})) {
		width: auto;
	}
`;

export const StyledBoxPoland = styled.div`
	display: flex;
	width: 100%;
	height: 100%;
	justify-content: center;
	flex-direction: column;
`;

export const Box = styled.div`
	display: flex;
	width: 100%;
	gap: 10px;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;
	@media ((max-width: ${({ theme }) => theme.breakpoint.lg})) {
		flex-direction: column;
		height: 100%;
	}
`;

export const StyledMapPoland = styled.div`
	display: flex;
	height: 100%;
	margin: auto;
	min-width: 65%;
	align-content: center;
	flex-direction: column;
	place-content: center;
`;

export const Header = styled.span`
	margin: 0 auto 10px auto;
	text-transform: uppercase;
	margin-bottom: 10px;
	text-align: center;
`;

export const StyledMap = styled.div`
	display: flex;
	flex: 0 0 50%;
	max-width: 500px;
	max-height: 500px;
	justify-content: flex-end;
	align-content: center;
	flex-direction: row;
`;

export const StyledSvg = styled.svg`
	@media (min-width: ${({ theme }) => theme.breakpoint.xxl}) {
		max-height: 600px;
	}
	@media ((max-width: ${({ theme }) => theme.breakpoint.md})) {
		max-width: 400px;
	}

	/* @media ((min-width: ${({ theme }) =>
		theme.breakpoint.xl}) and (max-width:${({ theme }) =>
		theme.breakpoint.xxl})){
           max-height:400px;
        };

@media ((min-width: ${({ theme }) => theme.breakpoint.lg}) and (max-width:${({
		theme,
	}) => theme.breakpoint.xl})){
           max-height:100px;
        };

@media ((min-width: ${({ theme }) => theme.breakpoint.md}) and (max-width:${({
		theme,
	}) => theme.breakpoint.lg})){
           max-height:100px;
        };

@media ((min-width: ${({ theme }) => theme.breakpoint.sm}) and (max-width:${({
		theme,
	}) => theme.breakpoint.md})){
           max-height:100px;
        };

@media ((max-width:${({ theme }) => theme.breakpoint.sm})){
           max-height:100px;
        }; */

	& path:hover {
		/* transform: scale(1.1); */
		transform-origin: 50% 50%;
		transform-box: fill-box;
		/* position: relative;
  z-index:4 */
	}
	& path:active {
		fill: #9d0c26;
	}
	& a:hover {
		outline: none;
		transition: all 0.5s ease-in-out;
	}
`;

export const Link = styled(NavLink)`
	text-decoration: none;
	text-decoration-line: none;
`;

export const StyledList = styled.div`
	display: flex;
	max-width: 35%;
	width: auto;
	background-color: white;
	padding: 10px;
	border: 1px solid rgb(203, 203, 203);
	border-radius: 20px 0 0 20px;
	max-height: 95%;
	overflow-y: scroll;
	scrollbar-width: thin;
	word-break: break-word;

	@media (min-width: ${({ theme }) => theme.breakpoint.xxl}) {
		max-height: 400px;
	}

	@media ((max-width: ${({ theme }) => theme.breakpoint.xxl})) {
		font-size: 14px;
	}

	@media ((max-width: ${({ theme }) => theme.breakpoint.xl})) {
		max-height: 300px;
	}

	@media ((max-width: ${({ theme }) => theme.breakpoint.lg})) {
		font-size: 12px;
		display: none;
	}

	&::-webkit-scrollbar {
		width: 6px;
	}
	&::-webkit-scrollbar-thumb {
		background: teal;
		border-radius: 10px;
		border: 1px solid black;
		height: 100px;
	}
`;

export const StyledUl = styled.ul`
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: fit-content;
	margin: auto;
	padding: 0;
	text-decoration: none;
	text-decoration-line: none;
	list-style-type: none;
`;

export const StyledLi = styled.li`
	display: flex;
	text-decoration: none;
	text-decoration-line: none;
	padding: 5px;
	&:hover {
		font-weight: bold;
		cursor: pointer;
		background-color: rgb(240, 240, 240);
	}
	&:active {
		color: #808080;
	}
	${({ active }) =>
		active &&
		css`
			 {
				fill: black;
				background-color: rgba(0, 0, 0, 0.17);
				&:hover {
					fill: black;
					color: black;
					background-color: rgba(0, 0, 0, 0.27);
				}
			}
		`};
`;

export const StyledSelect = styled.div`
	display: flex;
	gap: 5px;
	justify-content: center;
	align-items: center;
	color: black;
	margin: 0 auto;
	margin-top: 10px;
	text-decoration: none;
	text-decoration-line: none;
	width: fit-content;
`;

export const StyledUnitBox = styled.div`
	display: flex;
	flex-direction: row;
	align-content: center;
	justify-content: center;
	align-items: center;
`;

export const StyledCheck = styled.div`
	max-width: 27px;
	@media ((max-width: ${({ theme }) => theme.breakpoint.md})) {
		max-width: 20px;
	}
`;

export const StyledRegionNameLabel = styled.div`
	text-transform: uppercase;
	text-align: center;
`;

export const StyledProvinceButtonBox = styled.div`
	display: flex;
	flex-direction: row;
	align-content: center;
	align-items: center;
	justify-content: space-between;
	gap: 5px;
	width: 100%;
`;

export const StyledHeaderText = styled.span`
	display: flex;
	text-align: center;
	text-transform: uppercase;
	margin: auto;
`;
